 <template>
    <section class="app-header">
        <div v-if="isOpen" class="nav-modal">
            <div class="content">
                    <router-link to="/">HOME</router-link>
                    <router-link to="/stats">STATS</router-link>
                    <router-link to="/admin">ADMIN</router-link>
            </div>
        </div>

        <div class="logo">
            <img src="https://res.cloudinary.com/dw85wdwsw/image/upload/v1658149893/Portfolio/wjjzokfmoonrxd0gb0zo.png"
                alt="">
            The Garden
        </div>
        <nav>
            <router-link to="/">HOME</router-link>
            <router-link to="/stats">STATS</router-link>
            <router-link to="/admin">ADMIN</router-link>
        </nav>
        <div class="shopping-cart">
            <button class="dropbtn"><svg fill="white" xmlns="http://www.w3.org/2000/svg" height="24" width="24">
                    <path
                        d="M7 22q-.825 0-1.412-.587Q5 20.825 5 20q0-.825.588-1.413Q6.175 18 7 18t1.412.587Q9 19.175 9 20q0 .825-.588 1.413Q7.825 22 7 22Zm10 0q-.825 0-1.412-.587Q15 20.825 15 20q0-.825.588-1.413Q16.175 18 17 18t1.413.587Q19 19.175 19 20q0 .825-.587 1.413Q17.825 22 17 22ZM6.15 6l2.4 5h7l2.75-5ZM5.2 4h14.75q.675 0 .925.5t-.025 1.05l-3.55 6.4q-.275.5-.725.775-.45.275-1.025.275H8.1L7 15h12v2H7q-1.125 0-1.7-.988-.575-.987-.05-1.962L6.6 11.6 3 4H1V2h3.25Zm3.35 7h7Z" />
                </svg>
                {{ cartProducts.length }}
            </button>
            <div class="mq-nav">
                <svg @click="openNav()" xmlns="http://www.w3.org/2000/svg" fill="" height="40" width="40">
                    <path d="M5 30v-2.792h30V30Zm0-8.625v-2.75h30v2.75Zm0-8.583V10h30v2.792Z" />
                </svg>
            </div>
            <div v-if="cartProducts.length > 0" class="dropdown-content">
                <div class="cart-products" v-for="product in cartProducts" :key="product">
                    <div class="products">
                        <p>{{ product.title }}</p>
                        <p>{{ product.price }} $</p>
                    </div>
                </div>
                <div class="total">
                    <p>Total: </p>
                    <p>{{ cartTotal }} $</p>
                </div>
                <button @click="pay">Checkout</button>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    name: 'app-header',
    data() {
        return {
            isOpen: false,
        }
    },
    methods: {
        pay() {
            this.$store.dispatch({ type: 'pay', cart: this.cartProducts, total: this.cartTotal })
        },
        openNav() {
            this.isOpen = !this.isOpen
        },
        closeNav() {
            this.isOpen = false
        },
    },
    computed: {
        cartProducts() {
            return this.$store.getters.cart
        },
        cartTotal() {
            return this.$store.getters.cartTotal
        },
    }
}
</script>