import { createStore } from 'vuex'
import { plantService } from '../services/plant-service.js'
import { transactionService } from '../services/transaction-service.js'

export default createStore({
  state: {
    plants: null,
    allPlants: null,
    transactions: null,
    cart: [],

  },
  getters: {
    plants(state) {
      return state.plants
    },
    allPlants(state) {
      return state.allPlants
    },
    transactions(state) {
      return state.transactions
    },
    cart(state) {
      return state.cart
    },
    cartLength(state) {
      return state.cart.length
    },
    cartTotal({ cart }) {
      return cart.reduce((acc, prd) => acc + prd.price, 0)
    },
    mostSold(state){
      return state.transactions
      // return state.cart.length
    }
  },
  mutations: {
    setTransactions(state, {transactions}) {
      state.transactions = transactions;
    },
    setPlants(state, {plants}) {
      state.plants = plants;
    },
    setAllPlants(state, {plants}) {
      state.allPlants = plants;
    },
    removePlant(state, {id}) {
      const idx = state.plants.findIndex((plant) => plant._id === id)
      state.plants.splice(idx, 1)
    },
    savePlant(state, {plant}) {
      const idx = state.plants.findIndex((currPlant) => currPlant._id === plant._id)
      if (idx !== -1) state.plants.splice(idx, 1, plant)
      else state.plants.push(plant)
    },
    addToCart(state, { product }) {
      state.cart.unshift(product)
    },
   clearCart(state) {
    state.cart = []
    },
  },
  actions: {
    async loadPlants({ commit }) {
      try {
        let plants = await plantService.query()
        commit({type: 'setPlants', plants});
      } catch (err) {
        console.log('Error cant load plants');
      }
    },
    async loadTransactions({ commit }) {
      try {
        let transactions = await transactionService.query()
        commit({type: 'setTransactions', transactions});
      } catch (err) {
        console.log('Error cant load transactions');
      }
    },
    async removePlant({ commit }, {id}) {
      try {
        await plantService.remove(id)
        commit({type: 'removePlant', id});
      } catch (err) {
        console.log('Error cant remove plant');
      }
    },
    async savePlant({ commit }, { plant }) {
      try {
        await plantService.save(plant)
        commit({type: 'savePlant' , plant });
      } catch (err) {
        console.log('Error cant save plant');
      }
    },
    async pay({ commit }, { cart , total }) {
      try {
        let transaction =  transactionService.makeTransaction(cart , total)
        await transactionService.save(transaction)
        commit({type: 'clearCart'});
      } catch (err) {
        console.log('Error cant update transaction');
      }
    },
  },
  modules: {
}
})