import {
    httpService
} from './http.service.js'

export const transactionService = {
    query,
    save,
    makeTransaction,
}

async function query() {
    try {
        return await httpService.get('transaction')
    } catch (err) {
        console.log('query err:', err);
    }
}

async function save(transaction) {
    try {
        return await httpService.post(`transaction/`, transaction)
    } catch (err) {
        console.log('save err:', err);
    }
}

function makeTransaction(cart, total) {
    let idCart = cart.map((plant) => plant._id)
    let transaction = {
        date: new Date(Date.now()).toString(),
        formattedDate: _makeDate(),
        products: cart,
        productsIds: [...new Set(idCart)],
        total: total
    }
    return transaction
}

function _makeDate() {
    let today = new Date();
    let dd = String(today.getDate()).padStart(2, '0');
    let mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    let yyyy = today.getFullYear();
    today = dd + '.' + mm + '.' + yyyy;
    return today
}