import {
    httpService
} from './http.service.js'

export const plantService = {
    query,
    save,
    remove,
    getEmptyPlant,
}


async function query() {
    try {
        return await httpService.get('plant')
    } catch (err) {
        console.log('query err:', err);
    }
}

async function remove(plantId) {
    try {
        return await httpService.delete(`plant/${plantId}`)
    } catch (err) {
        console.log('remove err:', err);
    }
}

async function save(plant) {
    try {
        if (plant._id) {
            return await httpService.put(`plant/${plant._id}`, plant)
        } else {
            return await httpService.post(`plant/`, plant)
        }
    } catch (err) {
        console.log('save err:', err);
    }
}

function getEmptyPlant() {
    return {
        title: '',
        price: '',
        description: '',
        img: '',
    }
}