<template>
  <div class="home">
    <div class="plants-home">
      <div class="plant" v-for="plant in plants" :key="plant._id">
        <div class="plant-img">
          <img :src="plant.img" alt="">
          <div class="img-on-hover">
            <p>{{ plant.description }}</p>
          </div>
        </div>
        <h2>{{ plant.title }}</h2>
        <!-- <p>{{ plant.description }}</p> -->
        <div class="price-cart">
          <h4>{{ plant.price }} USD</h4>
          <button @click="buyPlant(plant)">+ Add To Cart</button>

        </div>
      </div>
    </div>
  </div>
</template>

<script>


export default {
  name: 'HomeView',
  methods: {
    buyPlant(plant) {
      this.$store.commit({ type: 'addToCart', product: plant })
    },
  },
  computed: {
    plants() {
      return this.$store.getters.plants
    },
  },
  components: {
  }
}
</script>
